import React, { FunctionComponent, ReactNode, useEffect, useState } from 'react'
import { useTransition, animated } from 'react-spring'

const PageTransition:FunctionComponent = ({ children }) => {
  const [page, setPage] = useState<ReactNode>(null)

  const transitions = useTransition(page, {
    from: { opacity: 0},
    enter: { opacity: 1},
    leave: { opacity: 0 },
    config: { 
      duration: 500,
      easing: x => x * x * x * x
    },
    reset: true
  })

  useEffect(() => {
    if (!page || page !== children) setPage(children)
  }, [children])

  return transitions(
    (styles, item) => item && <animated.div style={styles}><div>{item}</div></animated.div>
  )
}

export default PageTransition